<template>
    <f7-page @page:afteranimation="afterAnimation">
        <f7-navbar>
            <f7-nav-left>
                <f7-link back sliding></f7-link>
            </f7-nav-left>
            <f7-nav-center></f7-nav-center>
            <f7-nav-right>
                <f7-link></f7-link>
            </f7-nav-right>
        </f7-navbar>
        <div class="content signup" inner>
            <h1 class="title">What's your first name?</h1>
            <div class="center-content">
                <f7-input type="text" id="first_name" placeholder="First name" v-model="first_name"
                          :class="{'text-center' : !!first_name}"
                          @focus="handlerFocus()" @blur="handleBlur()"/>
            </div>
            <div class="bottom">
                <f7-button class="actions-modal-button" href="/signup_03/">NEXT</f7-button>
                <f7-button class="actions-modal-button" href="/signup_02_no_anim/" :animate-pages="false">NO ANIM</f7-button>
            </div>
        </div>
    </f7-page>
</template>

<script>
    export default {
        methods: {
            afterAnimation: function () {
                console.log('after anim');
                $('#first_name').focus().click();
//                this.$router.reload({url: '/signup_02_no_anim/', pushState: false, animatePages: false,
//                    ignoreCache: true,
//                    reload: true})
            },
            handlerFocus: function () {
                let iOS = /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
                if(iOS)
                this.$data.$$('div.bottom').css('border-bottom', '40vh solid transparent');
            },
            handleBlur: function () {
                let iOS = /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
                if (iOS)
                    this.$data.$$('div.bottom').css('border-bottom', '0vh solid transparent');
            }
        },
        data: function () {
            return {
                first_name: null,
                $$: Dom7,
            }
        }
    };
</script>


